@use '../../common/styles/mixins' as *;
$form-border: 1px solid #000;

.Bundle {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;

  @media screen and (min-width: 1008px) {
    flex-direction: row;
    padding: 0 var(--padding-desktop, 4rem) 4rem var(--padding-desktop, 4rem);
  }

  &__media {
    width: 100%;

    @media screen and (min-width: 1008px) {
      width: 58%;
      position: sticky;
      top: 0;
      left: 0;
    }

    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    img {
      max-width: 100%;
      width: 100%;
      height: 100%;
    }
  }

  &__child-images-container {
    width: 100%;
    height: 0;
    padding-bottom: 91%;
    position: relative;
    margin: 0;

    img {
      width: 100%;
      height: auto;
    }

    #box-image:first-child {
      z-index: 99;
    }

    @media screen and (max-width: 1007px) {
      padding-bottom: 100%;
    }
  }

  &__custom-image {
    width: 40%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    overflow: hidden;
    margin: 0;
  }

  &__images-list {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 1.25%;
    overflow: hidden;
    left: 0;
  }

  &__child-image {
    top: 92%;
    left: 0;
    transform-origin: top;
    transition: all 0.65s ease;
    transform: translateY(var(--end-y-transform));
    animation: transformFromTop 0.65s ease;
    z-index: -1;
    position: absolute;
  }

  &__default-images {
    margin: 0;
    padding: 0;
  }

  &__media-item {
    position: relative;
    height: 0;
    padding-bottom: 100%;
  }

  &__loading-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    filter: blur(12px);
  }

  &__form {
    display: flex;
    flex-direction: column;
    position: sticky;
    top: 0;
    z-index: 1000;
    width: 100%;
    max-width: 50rem;
    padding: 1.6rem var(--padding-mobile, 2rem) 4rem var(--padding-mobile, 2rem);
    margin: 0 auto;

    @media screen and (min-width: 1008px) {
      padding: 4rem 1.8rem 0 5.8rem;
      width: 42%;
      min-width: 42%;
      max-width: none;
    }
  }

  &__form-footer {
    position: sticky;
    bottom: 0;
    left: 0;
    background: #fff;
    z-index: 9;
    padding: 1rem;
    margin-inline: -1rem;
  }

  &__form-head,
  &__form-body {
    margin-bottom: 2rem;
    border-bottom: $form-border;
    padding-bottom: 2rem;
  }

  &__product-form {
    display: none;
    align-items: center;
    gap: 5px;
    margin-top: 10px;
    justify-content: center;

    button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 25px;
      height: 25px;
    }

    input {
      max-width: 40%;
      text-align: center;
      border: 1px solid #c6c2c2;
      font-size: 15px;
      padding: 5px;

      &::-webkit-outer-spin-button,
      &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }

      &[type='number'] {
        -moz-appearance: textfield;
      }
    }
  }

  &__title.h5 {
    margin: 0 0 1rem 0;
    color: #1c1c1c !important;
    font-family: 'GalanoGrotesqueRegular', sans-serif;
    line-height: 1.5em;

    @include respond(tablet) {
      margin-bottom: 1rem;
    }
  }

  &__price {
    font-family: var(--body-font)!important;
    font-weight: 500;
    text-transform: uppercase;

    @include respond(tablet) {
      font-size: 1.76rem;
    }

    &--compare {
      color: var(--silver);
      text-decoration: line-through;
    }
  }

  &__price-group {
    display: flex;
    align-items: center;

    @include respond(tablet) {
      margin-bottom: 0.3rem;
    }
  }

  &__price-group.empty-price &__price,
  &__price-group.empty-price + &__discount {
    display: none;
  }

  &__price-group:not(.empty-price) &__price--placeholder {
    display: none;
  }

  &__discount {
    display: none;
    align-items: center;
    justify-content: flex-start;
    gap: 1rem;
    margin-top: 0.5rem;
    font-size: 12px;

    @media screen and (min-width: 1008px) {
      font-size: 14px;
    }
      
    svg {
      width: 11px;
      height: 11px;
      @media screen and (min-width: 1008px) {
        width: 13px;
        height: 13px;
      }
    }
  }

  &__price {
    font-family: var(--body-font) !important;
    font-weight: 500;
    text-transform: uppercase;

    @include respond(tablet) {
      font-size: 1.76rem;
    }

    &--compare {
      color: var(--silver);
      text-decoration: line-through;
    }

    &:not(:last-child):not(:empty) {
      margin-right: 1.5rem;
    }
  }

  &__option-values {
    padding: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    margin-bottom: 3rem;

    li {
      padding: 0.5rem 1rem;
      box-shadow: 0 0 0 1px #000;
      cursor: pointer;

      &:not(.active):hover {
        box-shadow: 0 0 0 2px var(--dark-silver);
      }

      &.active {
        color: #fff;
        background-color: #000;
      }
    }
  }

  &__error-container {
    font-size: 17px;
    text-align: right;

    &:is(:empty) {
      display: none;
    }
  }

  &__products {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(95px, 110fr));
    gap: 1.5rem;
    min-height: 240px;
    
    @media screen and (min-width: 1008px) {
      grid-template-columns: repeat(auto-fit, minmax(115px, 130fr));
    }
  }

  &__products--maxed &__product:not(.active) {
    opacity: 0.7;
    cursor: default;
    pointer-events: none;
  }

  &__product.active &__product-content {
    box-shadow: 0 0 0 1.5px #000;
  }

  &__product.active &__product-form {
    display: flex;
  }

  &__product-content {
    position: relative;
    box-shadow: 0 0 0 1px var(--silver);
    overflow: hidden;
    cursor: pointer;
    height: 0;
    padding-bottom: 135%;

    &:hover {
      box-shadow: 0 0 0 1.5px #000;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      object-fit: cover;

      &:nth-child(2) {
        z-index: 0;
        filter: blur(8px);
      }
    }
  }

  &__quantity {
    font-size: 1.8rem;
    font-family: var(--heading-font-big), sans-serif;
    display: flex;
    align-items: center;
    gap: .5rem;

    @media screen and (min-width: 1008px) {
      font-size: 2rem;
    }

    svg {
      margin-right: 1rem;
    }
  }

  &__cta-wrapper {
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    gap: 1rem;

    .klarna-pay--wrapper {
      grid-column: 1/3;
    }
  }

  &__details {
    border-top: 1px solid #000;
    margin-top: 2rem;
    padding-top: 2rem;
    font-size: 12px;

    &--title {
      font-size: 14px;
      text-transform: uppercase;
      margin-block: 0 2rem;
    }
  }
}

@keyframes transformFromTop {
  from {
    transform: translateY(-450%);
    opacity: 0.5;
  }
  to {
    transform: translateY(var(--end-y-transform));
    opacity: 1;
  }
}

#chat-button {
  bottom: 100px !important;
}